
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import { validateNotNull, validateUpload } from 'util/rules'
import Uploader from 'assembly/uploader/uploader'

export default {
data () {
return {
baseUrl: process.env.FMS_SERVICE_IP,
img8: require('../../assets/images/kdth.jpeg'),
dialogVisible: false,
image: '',
orderInfo: '',
typeText: '',
withdraw: false,
backExpressNo: '',
backExpressCompany: '',
photoLabel: '',
photoArr1: [{
text: '物流标签(正面)',
img: 'img1'
}, {
text: '重量喷码(背面)',
img: 'img2'
}, {
text: '箱单内容(箱内)',
img: 'img3'
}, {
text: '多发实物',
img: ''
}, {
text: '整箱称重',
img: 'img4'
}],
photoArr2: [{
text: '物流标签(正面)',
img: 'img1'
}, {
text: '重量喷码(背面)',
img: 'img2'
}, {
text: '箱单内容(箱内)',
img: 'img3'
}, {
text: '错发实物',
img: ''
}, {
text: '整箱称重',
img: 'img4'
}],
photoArr3: [{
text: '物流标签(正面)',
img: 'img1'
}, {
text: '重量喷码(背面)',
img: 'img2'
}, {
text: '箱单内容(箱内)',
img: 'img3'
}, {
text: '整箱完整全图',
img: 'img5'
}, {
text: '整箱称重',
img: 'img4'
}],
photoArr4: [{
text: '产品破损照片',
img: ''
}, {
text: '整箱完整全图',
img: ''
}, {
text: '物流标签(正面)',
img: 'img1'
}, {
text: '补充照片',
img: ''
}],
photoArr6: [{
text: '产品缺陷照片',
img: ''
}, {
text: '补充照片',
img: ''
}, {
text: '补充照片',
img: ''
}, {
text: '补充照片',
img: ''
}],
photoArr: [{
text: '产品退回快递单照片',
img: 'img8'
}],
photos: ['', '', '', '', '']
}
},
methods: {
goBack () {
this.$router.push({
path: '/afterSaleList',
query: {
index: this.$route.query.index
}
})
},
showImg (src) {
this.image = src
this.dialogVisible = true
},
showImg1 (img) {
this.image = this[img]
this.dialogVisible = true
},
toOrder (newOrderNo) {
this.$router.push({
path: '/orderDetail',
query: {
orderNo: newOrderNo
}
})
},
setType (type) {
switch (type) {
case 1:
this.typeText = '多发'
break
case 2:
this.typeText = '错发'
break
case 3:
this.typeText = '漏发'
break
case 4:
this.typeText = '到货残次'
break
case 6:
this.typeText = '质量评估'
break
}
this.photoLabel = this['photoArr' + type]
},
setClientStatus (clientStatus) {
let clientStatusText = ''
switch (clientStatus) {
case 1:
clientStatusText = '审核中'
break
case 2:
clientStatusText = '已完成'
break
case 3:
clientStatusText = '未通过'
break
case 4:
clientStatusText = '已撤回'
break
}
return clientStatusText
},
setStatus (status) {
let statusText = ''
switch (status) {
case 1:
statusText = '待物流审核'
break
case 3:
statusText = '物流已审核'
break
case 4:
statusText = '物流不同意'
break
case 5:
statusText = '客服不同意'
break
case 6:
case 7:
statusText = '客服已审核'
break
case 9:
statusText = '未能生成售后订单'
break
case 10:
statusText = '特殊处理'
break
case 11:
statusText = '已生成售后订单'
break
}
return statusText
},
beforeSend (file) {
if (validateUpload(this, file)) {
this.$vux.loading.show({
text: '图片上传中，请稍候'
})
return true
}
},
onFinish (res) {
this.$vux.loading.hide()
this.photos.splice(4, 1, res.model)
},
onError () {
this.$vux.loading.hide()
this.$vux.toast.text('上传失败，请重试~')
},
submit () {
if (validateNotNull(this, this.backExpressCompany, '快递公司') && validateNotNull(this, this.backExpressNo, '快递单号') && validateNotNull(this, this.photos[4], '产品退回快递单照片')) {
let data = [{
backExpressCompany: this.backExpressCompany,
backExpressNo: this.backExpressNo,
photos: this.photos,
id: this.id
}]
this.$tupHttp(this, 'front/declaration/updateDeclaration', 'POST', data).then(res => {
this.$vux.toast.text('提交成功')
setTimeout(() => {
this.$router.go(-1)
}, 1000)
})
}
},
toEdit () {
this.$router.push({
path: '/afterSaleEdit',
query: {
index: 0,
id: this.id
}
})
},
toWithdraw () {
this.withdraw = true
},
onWithdraw () {
let data = [{
status: 2,
id: this.id
}]
this.$tupHttp(this, 'front/declaration/updateDeclaration', 'POST', data).then(res => {
this.$vux.toast.text('撤回成功')
setTimeout(() => {
this.$router.push({
path: '/afterSaleList',
query: {
index: this.$route.query.index
}
})
}, 1000)
})
},
_getDetail () {
this.$tupHttp(this, 'front/declaration/getDeclaration?id=' + this.id).then(res => {
this.orderInfo = res.data.model
this.setType(this.orderInfo.type)
this.orderInfo.clientStatusText = this.setClientStatus(this.orderInfo.clientStatus)
this.orderInfo.statusText = this.setStatus(this.orderInfo.status)
this.orderInfo.createTime = new Date(this.orderInfo.createTime).format('yyyy-MM-dd hh:mm:ss')
this.orderInfo.receivedDate = this.orderInfo.receivedDate ? new Date(this.orderInfo.receivedDate).format('yyyy-MM-dd hh:mm:ss') : this.orderInfo.receivedDate
this.orderInfo.logisticsAuditTime = this.orderInfo.logisticsAuditTime ? new Date(this.orderInfo.logisticsAuditTime).format('yyyy-MM-dd hh:mm:ss') : this.orderInfo.logisticsAuditTime
this.orderInfo.serviceAuditTime = this.orderInfo.serviceAuditTime ? new Date(this.orderInfo.serviceAuditTime).format('yyyy-MM-dd hh:mm:ss') : this.orderInfo.serviceAuditTime
this.backExpressCompany = this.orderInfo.backExpressCompany
this.backExpressNo = this.orderInfo.backExpressNo
this.photos = this.$util.deepClone(this.orderInfo.photos)
if (this.orderInfo.exchangeProductVos) {
this.orderInfo.exchangeProductVos = this.orderInfo.exchangeProductVos.map((item) => {
item.supplyTime = item.supplyTime ? new Date(item.supplyTime).format('yyyy-MM-dd hh:mm:ss') : item.supplyTime
return item
})
}
})
}
},
created () {
this.id = this.$route.query.id
if (this.id) {
this._getDetail()
}
},
components: {
XInput,
Group,
XButton,
XHeader,
Datetime,
Cell,
Actionsheet,
XTextarea,
Uploader,
CellFormPreview,
InlineXNumber,
Confirm,
XDialog
}
}



